import React from 'react'






const Filter = (props) => {

   
    const persons = props.persons 
    const setPersons = props.setPersons
    const tempPersonList = props.tempPersonList
    let searchInput = props.searchInput


    const searchFilter = (event) =>{
        

        if(event.target.value.length !== 0){
        
          searchInput = true;
          const returnSearch = persons.filter((person) =>{
    
              return person.name.includes(event.target.value)
    
            })
          
          setPersons(returnSearch)
          console.log(searchInput)
    
        } else {
          setPersons(tempPersonList) //Return back to Original List to display
          searchInput = false
    
        }
    
      }


    return(
    <div>

    <h1>
        Search Person
    </h1>

    <div>

        Search <input onChange ={searchFilter}/>
        
    </div>


    </div>
    )
}

export default Filter