import React from 'react'
import Axios from 'axios'
import '../index.css'
const url = '/api/persons'




const Persons = (props) => {

    const persons = props.persons
    const setPersons = props.setPersons
    const tempPersonList = props.tempPersonList
    let searchInput = props.searchInput


    /*Whoever reads this code I repeated myself and I had (& still)
      no idea how to pass an argument to the onlick with just keeping a reference
    */

    let returnList = Object.keys(persons).map((person) => <li className='contact' key = {person.number}>{person.name} : {person.number}  <button onClick={
        () =>{  Axios.delete(`${url}/${person.id}`)
        .then((response) =>{
         
            setPersons(persons.filter(personCheck => personCheck.id !== person.id ))


        })}}>Remove</button></li>)

    let returnSearchList = tempPersonList.map((person) => <li key = {person.number}>{person.name} : {person.number}  <button onClick={
        () =>{  Axios.delete(`${url}/${person.id}`)
        .then((response) =>{
         
            setPersons(persons.filter(personCheck => personCheck.id !== person.id ))


        })}}>Remove</button></li>)
    

    return(
    <div>
        <h2>
            Numbers
        </h2>
    {searchInput ? returnSearchList   : returnList} 

    </div>
    )
}


export default Persons